#home-landing-page {
    .main-photo {
        @extend %img-cover;
        height: 60vh;
        min-height: 250px;
        max-height: 100%;
        background-image: url("../img/photo/group.jpg");
        background-position: 47% 30%;
    }
    .main-info {
        padding: 60px 20px;
        background-color: #E5E5EA;
        @extend %Grid-column;
        @extend %Grid-column--center;
    }
    .bg-img {
        position: absolute;
        width: 50vw;
        height: 50vh;
        min-width: 150px;
        min-height: 150px;
        left: -10vw;
        bottom: -10vh;
        @extend %img-contain;
        background-image: url("../img/flower03.png");
        opacity: 0.8;
    }
    .office-hours {
        z-index: 999;
        h2 {
            margin-bottom: 40px;
        }  
        .weekday {
            width: 60px;
        }
        .time {
            margin-right: 20px;
            margin-bottom: 20px;
        }
        .note {
            color: red;
            font-weight: 500 !important;
            line-height: 1.5;
        } 
    }
    @media (min-width: $screenM) {
        .main-photo {
            height: 50vh;
            min-height: 350px;
        }
        .main-info {
            padding: 80px 160px;
            @include align-items(flex-end);
            @include justify-content(center);
        }
    }
    @media (min-width: $screenXL) {
        height: calc(100vh - 230px);
        min-height: 600px;
        @include flex-direction(row-reverse);
        .main-photo {
            height: 100%;
            background-position: 45% 30%;
        }
        .main-info {
            padding: 100px;
        } 
    }
}

.section {
    padding: 60px 0;
    .section-title {
        color: $main-blue;
        margin-bottom: 20px;
    }
    .section-des {
        text-align: center;
        margin-bottom: 40px !important;
    }
    .highlight-info {
        @extend %underline;
        color: black;
        margin: 10px 20px;
        h3 {
            margin: 10px 0;
        }
    }
    @media (min-width: $screenL) {
        padding: 100px 0;
    }
}
#home-drs {
    .section-des {
        @extend %Grid-column;
        @extend %Grid-column--center;
    }
    .col-dr {
        @include flex(100%);
        margin-bottom: 40px;
    }
    .dr-photo {
        z-index: -999;
        width: 40vw;
        min-width: 150px;
        max-width: 300px;
        height: 60vh;
        min-height: 200px;
        max-height: 400px;
        @extend %img-cover;
        background-position: 50% 0%;
    }
    .dr-info {
        .dr-info-name {
            z-index: 100;
        }
    }
    @media (min-width: $screenXS) {
        .section-des {
            @include flex-flow(row, nowrap);
        } 
    }
    @media (min-width: $screenL) {
        .col-dr {
            @include flex(1);
            margin: 0 4vw 0 0;
            &:last-child {
                margin-right: 0;
            }
        }
        .dr-photo {
            width: 100%;
            max-width: 600px;
            height: 60vh;
            min-height: 300px;
            max-height: 600px;
        }
        .dr-info {
            margin-top: -150px;
            position: relative;
            width: 100%;
            height: 150px;
            .bg-img {
                z-index: -100;
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                left: 0;
                background-color: $white-60;
                @include flex(1);
            }
        }
    }
} 

#home-video {
    text-align: center;
}

